body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.primaryColor{
    color: #5E4FA2;
}

html {
    scroll-behavior: smooth;
}

body{
    background-color: #f8f9fa;
}

/* Hero Section Styles */

.fullElipse{
    position: absolute ;
    height : auto;
}

.pathStyle {
    position: absolute;
    top : 0;
    right : 0;
}

.halfEStyle {
    position: absolute;
    bottom : 0;
    left : 0;
    height: 30%;
    width: auto;
}

.imageStyle {
    height : 75%;
    width : auto ;
    margin-left: 30%;
}

.h1Style {
    color: #5E4FA2;
    font-weight: 700;
    font-family: 'Montserrat', sans-serif;
    font-size: 55px;
}

.pStyle  {
    color: #5E4FA2;
    font-family: 'Montserrat', sans-serif;
    margin-bottom: 5%;
    z-index: -99;
    font-size: 20px;
}

.heroStyle {
    background-color: #f8f9fa;
}

.wholeRow  {
    padding-top: 3%;
}

.wholeColumn {
    padding-top: 2%;
    padding-left: 10%;
}

.hrStyle {
    margin-bottom: 5%;
    border: 5px solid #FEC949;
}

.confDetails{
    width:260px;
    height: 120px;
    background-color: #ECE6F2;
    border-radius: 15px;
    z-index: 1;
    
}

.ShadowL{
    box-shadow: -9px 9px 21px rgba(0, 0, 0, 0.16);
}

.ShadowR{
    box-shadow: 9px 9px 21px rgba(0, 0, 0, 0.16);
}

.Shadow{
    box-shadow: 0px 9px 21px rgba(0, 0, 0, 0.16);
}

.confText{
    font-family: 'Poppins', sans-serif;
    width: 140px;
    text-align: center;
    color: #5E4FA2;
    font-weight: 700;
    font-size: 20px;
}

/* Card Section */
.cardTitle{
    color: #5E4FA2;
    font-size: 45px;
    font-weight: 700;
}

.keynotePath {
    position: absolute;
    right: 0;
    bottom: -50%;
    width: 15%;
    height: auto;
}

@media only screen and (max-width: 768px) {
    .imageStyle {
        height : 50%;
        width : auto ;
        margin-left: 20%;
        margin-top: 20%;
    }
    .h1Style {
        font-size: 40px;
    }
    .pStyle  {
        color: #5E4FA2;
        font-family: 'Montserrat', sans-serif;
        margin-bottom: 5%;
        z-index: -99;
        font-size: 18px;
    }

    .confDetails{
        -webkit-transform: scale(0.6);
                transform: scale(0.6);
        background-color: #ECE6F2;
        border-radius: 15px;
        z-index: 1;
        text-align: center;
    }

    .keynotePath {
        position: absolute;
        right: 0;
        bottom: 0;
        width: 50px;
        display: none;
    }

    .rightCard{
        margin-top: 5%;
    }
    

}
.card-box{
    position: relative;
    width: 280px;
    height: 325px;
    background-color: #ECE6F2;
    border-radius: 10px;
    box-shadow: -9px 9px 21px rgba(0, 0, 0, 0.16);
    transition: 0.7s;
    margin-bottom: 25%;
}

.card-box:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    background-color: #ECE6F2;
    border-radius: 10px;
    box-shadow: -20px 20px 21px rgba(0, 0, 0, 0.16);
    transition: 0.7s;
}

.avatar{
    width: 120px !important;
    height: 120px !important;
    border: 3px solid #FEC949;
    margin-top: 15%;
}

.profName{
    position: absolute;
    bottom : 28%;
    color: #5E4FA2;
    font-weight: 700;
}

.profDesc{
    position: absolute;
    top: 75%;
    width: 90%;
    font-size: 15px;
    color: #5E4FA2;
}

.card-image{
    width: 240px;
    height: auto;
    margin-top: 10%;
    border-radius: 10px;
}

.card-topic{
    margin-top: 10%;
    font-family: 'Poppins', sans-serif;
    color: #5E4FA2;
    font-weight: 700;
    font-size: large;
}

.card-text{
    font-family: 'Poppins', sans-serif;
    color: #5E4FA2;
    font-weight: 500;
    width: 75%;
    font-size: small;
}

.dateBack{
    background-color: #5E4FA2;
    color: #FEC949;
    border-radius: 9px;
    width: 150px;
    height: 50px;
    padding-left: 5%;
    padding-top: 4%;
    font-weight: 600;
}

